import React from "react";
import styled from "styled-components";
import { graphql } from "gatsby";
import Blog from "@components/page/Blog";
import { SeoContainer } from "@components/Container/seoContainer";
import { graphqlAllPostType } from "@tool/types";
import PostList from "@components/PostList";

type BlogListPropTypes = {
  data: graphqlAllPostType;
};

const Layout = styled.main`
  padding: 32px;
`;

export default function BlogList({
  data, // this prop will be injected by the GraphQL query below.
}: BlogListPropTypes) {
  const articles = data.articles.edges.map((v: any) => v.node);

  return <PostList articles={articles} RenderComponent={Blog} />;
}

export const pageQuery = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
    articles: allMarkdownRemark(
      filter: {
        fields: {
          sourceInstanceName: { eq: "blog" }
          locale: { eq: $language }
        }
      }
      sort: { order: DESC, fields: frontmatter___date }
    ) {
      edges {
        node {
          fields {
            name
          }
          frontmatter {
            title
            date
            image_cover
            type
            tags
          }
        }
      }
    }
  }
`;
export const Head = () => <SeoContainer subtitle="blog" pathname="/blog" />;
