import React from "react";
import styled from "styled-components";
import Post from "@components/Post";
import { device } from "@layouts/styleConfig";
import { MdFrontMatter } from "@tool/types";
import EmptyPost from "@components/Post/EmptyPost";

type BlogPropTypes = {
  list: Array<{ frontmatter: MdFrontMatter; fields: { name: string } }>;
};

const Layout = styled.div.withConfig({ componentId: "Blog_Layout" })`
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));

  @media ${device.tablet} {
    grid-template-columns: repeat(8, minmax(0, 1fr));
  }

  @media ${device.laptop} {
    margin: 0 10rem;
    grid-template-columns: repeat(12, minmax(0, 1fr));
  }
`;

export default function Blog({ list = [] }: BlogPropTypes) {
  return (
    <Layout>
      {list.length ? (
        list.map((post, index) => {
          const path = post.fields.name;
          const POST_DATA = post.frontmatter;
          return <Post key={index} data={{ ...POST_DATA, path }} />;
        })
      ) : (
        <EmptyPost />
      )}
    </Layout>
  );
}
