import React from "react";
import { Link } from "gatsby";
import styled from "styled-components";
import { colors, device } from "@layouts/styleConfig";
import { MdFrontMatter } from "@tool/types";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { useRecoilValue } from "recoil";
import { transformTextToHighlight } from "@tool/functions";
import Tag from "@components/Tag/Tag";
import { inputValueState } from "@state/index";

type PostType = {
  data: MdFrontMatter;
};

type layoutPropType = {
  [key: string]: string;
};

const Layout = styled.div.withConfig({
  componentId: "Post_Layout",
})`
  background-color: transparent;

  grid-column: span 4 / span 4;
  margin: 1rem 1.5rem;
  padding-bottom: 8px;
  border-bottom: 1px solid ${colors.lightWhite};

  transition-delay: 800;

  a {
    text-decoration: none;
    /* color: ${colors.white}; */
    width: 100%;
    transition: 0.3s;

    :hover {
      & .date,
      .post_title {
        color: ${({ theme }) => theme.hoverColor};
      }
      .post_tags {
        color: ${({ theme }) => theme.hoverColor};
        span {
          border-color: ${({ theme }) => theme.hoverColor};
        }
      }
    }
  }

  div.img_container {
    border-radius: 12px;
    overflow: hidden;
    width: 100%;
    height: 150px;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: relative;
    transition: 0.3s;
    opacity: ${({ theme }) => theme.opacity};
  }

  .date {
    margin-top: 8px;
    font-size: 16px;
    color: ${({ theme }) => theme.postColor};
    transition: 0.3s;
  }

  .post_title {
    margin-top: 4px;
    font-size: 20px;
    font-weight: 700;
    color: ${({ theme }) => theme.postColor};
    overflow-wrap: break-word;
    transition: 0.3s;
  }

  .post_tags {
    margin-top: 4px;
    color: ${({ theme }) => theme.postColor};
    display: flex;
    flex-wrap: wrap;
  }

  span.highlight {
    background-color: ${colors.highlight};
    color: ${colors.black};
  }

  @media ${device.tablet} {
    padding-bottom: 0;
    border-bottom: none;

    :hover {
      img {
        transform: scale(1.1);
        opacity: ${({ theme }) => theme.hoverOpacity};
      }
    }

    div.img_container {
      height: 200px;
    }

    .date {
      margin-top: 8px;
      font-size: 18px;
    }

    .post_title {
      font-size: 26px;
    }
  }

  @media ${device.laptop} {
    div.img_container {
      height: 250px;
    }

    .date {
      margin-top: 8px;
      font-size: 20px;
    }

    .post_title {
      font-size: 30px;
    }
  }
`;

export default function Post({ data }: PostType) {
  const { t } = useTranslation();
  const { image_cover, title, path, date, type, tags } = data;

  // const translatedTags = tags?.map((tag) => t(tag));

  const searchKeyValue = useRecoilValue(inputValueState);

  return (
    <Layout>
      <Link to={path}>
        <div className="img_container">
          <img alt="post_cover" src={image_cover} className="img_cover" />
        </div>
        {tags?.length !== 0 && (
          <div className="post_tags">
            {tags?.map((tag, index) => (
              <Tag
                key={`${title}-${tag}-${index}`}
                content={transformTextToHighlight(t(tag), searchKeyValue)}
              />
            ))}
          </div>
        )}
        <div className="date">{date.split("T")[0]}</div>
        <div className="post_title">
          {transformTextToHighlight(title, searchKeyValue)}
        </div>
      </Link>
    </Layout>
  );
}
