import { device } from "@layouts/styleConfig";
import React from "react";
import styled from "styled-components";
import { useTranslation } from "gatsby-plugin-react-i18next";

const EmptyLayout = styled.div.withConfig({ componentId: "EmptyLayout" })`
  grid-column: 1/-1;
  min-height: calc(100vh - 40px - 64px);
  box-sizing: border-box;

  display: flex;
  justify-content: center;
  align-items: center;

  @media ${device.tablet} {
    min-height: calc(100vh - 70px - 64px);
  }
`;

export default function EmptyPost() {
  const { t } = useTranslation();
  return (
    <EmptyLayout>
      <p> {t("found-nothing")}</p>
    </EmptyLayout>
  );
}
