import { postListArticleType } from "@tool/types";
import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { device, focusCss } from "@layouts/styleConfig";
import { MdSearch } from "react-icons/md";
import { useDebounce } from "@tool/useDebounce";
import { useSetRecoilState } from "recoil";
import { inputValueState } from "@state/index";

const Layout = styled.div.withConfig({ componentId: "PostList_Layout" })`
  label {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));

    div {
      width: 100%;
      position: relative;
      display: flex;
    }

    input {
      background-color: transparent;
      border: 1px solid ${({ theme }) => theme.color};
      border-radius: 4px;
      color: ${({ theme }) => theme.color};
      padding: 8px;
      font-size: 1rem;
      margin: 1rem 1.5rem;
      width: 100%;

      ${focusCss};
      :focus {
        outline-offset: 0;
      }
    }

    svg {
      color: ${({ theme }) => theme.color};
      position: absolute;
      top: 0;
      right: 30px;
      height: 100%;
      pointer-events: none;
    }
  }

  @media ${device.tablet} {
    label {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }

  @media ${device.laptop} {
    label {
      margin: 0 160px;
    }
  }
`;

type PostListPropTypes = {
  articles: postListArticleType[];
  RenderComponent: any;
};

export default function PostList({
  articles,
  RenderComponent,
}: PostListPropTypes) {
  const { t } = useTranslation();

  const setSearchKeyValue = useSetRecoilState(inputValueState);

  const [inputValue, setInputValue] = useState("");

  const searchKey = useDebounce(inputValue, inputValue.length > 0 ? 200 : 500);

  useEffect(() => {
    setSearchKeyValue(searchKey);
  }, [searchKey]);

  const filteredArticles =
    searchKey === ""
      ? articles
      : articles.filter((article: postListArticleType) => {
          return (
            article.frontmatter.title
              .toLocaleLowerCase()
              .includes(searchKey.toLocaleLowerCase()) ||
            article.frontmatter.tags.some((tag) =>
              t(`tags:${tag}`).toLocaleLowerCase().includes(searchKey)
            )
          );
        });

  return (
    <Layout>
      <label htmlFor="">
        <div>
          <input
            type="text"
            value={inputValue}
            onChange={(e) => setInputValue(e.target.value)}
            placeholder={t("tags:filter-tags")}
          />
          <MdSearch />
        </div>
      </label>
      <RenderComponent list={filteredArticles} />
    </Layout>
  );
}
