import React from "react";
import styled from "styled-components";

const TagLayout = styled.span`
  border: 1px solid ${({ theme }) => theme.postColor};
  margin: 4px;
  box-sizing: border-box;
  border-radius: 8px;
  padding: 0 4px;
`;

type TagType = {
  content: string | (string | JSX.Element)[];
};

export default function Tag({ content }: TagType) {
  return <TagLayout>{content}</TagLayout>;
}
